h1,h2,h3,h4{color:orangered}*{padding:0;margin:0;font-family:'Roboto Mono', monospace}a{text-decoration:none;cursor:pointer;color:lightseagreen}a:hover{color:orange}nav{display:flex;align-items:center;justify-content:space-between;padding:20px;background-color:white}#high-scores li{list-style:none}ul li{margin-left:25px}#wrapper{height:600px;width:900px;position:relative;margin:40px auto}#input-username{text-align:center;position:absolute;width:250px;margin:200px 325px}#input-username h1{font-size:40px;padding-bottom:20px}#input-username h2{font-size:20px;padding-bottom:20px}form{display:flex;justify-content:space-around}.hidden{display:none}form input,button{font-size:16px;padding:15px;background-color:white;color:teal;border:none;border-radius:30px}button:hover{background-color:orange}input:focus{outline:none}canvas{display:block;position:absolute;background:linear-gradient(0deg, #00aa7e, #00aa7e, #0080c8, #003b9d, #0080c8, #00aa7e, #00aa7e);background-size:700% 700%;-webkit-animation:AnimationName 30s  linear infinite;animation:AnimationName 30s  linear infinite}@-webkit-keyframes AnimationName{0%{background-position:50% 0%}100%{background-position:50% 100%}}@keyframes AnimationName{0%{background-position:50% 0%}100%{background-position:51% 100%}}.bottom-container{margin:0 auto;display:flex;justify-content:space-between;color:white}.bottom-container h3{color:white;padding-bottom:10px}.bottom-container section{padding:20px;width:50%;margin:50px;box-sizing:border-box;background-color:orangered;border:orangered solid 2px}
